import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MainPage } from './main.page';

const routes: Routes = [
  {
    path: '',
    component: MainPage,
    children: [
      { path: 'home', loadChildren: () => import('./tabs/home/home.module').then(m => m.HomePageModule) },
      { path: 'new-item', loadChildren: () => import('./tabs/new-item/new-item.module').then(m => m.NewItemPageModule) },
      { path: 'profile', loadChildren: () => import('./tabs/profile/profile.module').then(m => m.ProfilePageModule) },
      {
        path: '',
        redirectTo: '/home',
        pathMatch: 'full'
      }
    ],
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
})
export class MainPageRoutingModule {}
